
body {
  margin: 0;
  font-family: Arial;
  font-size: 15px;
}
.highlight {
  border-radius: 5px;
  background: orange;
}
.number {
  position: absolute;
  top: -30px;
  left: 50%;
  margin-left: -12px;
}
input {
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 1ch;
  padding: 4px;
  font-size: 100%;
  font-weight: inherit;
  outline: none;
}
input:invalid {
  color: red;
}
input[readonly] {
  border-color: white;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100vh;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.arc:first-child {
  margin-left: 35px;
}
.arc {
  position: relative;
  display: inline-block;
  margin-top: -80px;
}
.hidden {
  visibility: hidden;
}
canvas {
  position: absolute;
  height: 100%;
  width: 100%;
  bottom: 0;
  left: 0;
}
.formula {
  font-size: 200%;
  font-weight: bold;
  margin-bottom: 300px;
}
.ruler {
  width: 875px;
  background: url(/example-vue-axis/static/img/sprite.236a2c3.png);
  height: 83px;
}

